
import XButton from 'vux/src/components/x-button/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { mapState } from 'vuex';

export default {
components: {
XButton,
Group,
GroupTitle,
Cell,
XInput,
PopupPicker,
XNumber,
XTextarea
},
data() {
return {
sizeOption: [['A3', 'A4', 'B4', 'B5']],
colorOption: [['黑白', '彩色']],
pageOption: [['单面', '双面']],
compositeOption: [['2合1', '4合1', '6合1', '8合1', '9合1']]
};
},
computed: {
...mapState({
storeData: (state) => state.printData.storeData,
fileData: (state) => state.printData.fileData
})
},
methods: {
onSubmitSet() {
console.log(this.fileData);
this.$router.push({ path: '/payment' });
},
onBack() {
this.$router.push({
path: '/uploadfile'
});
}
},
created: function() {
console.log(this.storeData);
console.log(this.fileData);
if (!this.storeData.value || this.fileData.length == 0) {
this.$router.push({ path: '/chiocestore' });
return;
}
}
};



